
.SearchSection {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: baseline;
      align-items: baseline;
  -ms-flex-pack: start;
      justify-content: flex-start;
  -ms-flex-line-pack: center;
      align-content: center;
}

@media (max-width: 768px) {
  .SearchSection {
    -ms-flex-direction: column;
        flex-direction: column;
    -ms-flex-wrap: nowrap;
        flex-wrap: nowrap;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 0 auto;
  }
  .SearchForm {
    margin: 0 auto 10px;
  }
  .showing {
    margin: 0 auto;
  }
}

.SearchForm {
  border-bottom: 1px solid #2d2d2d;
  min-width: 280px;
}

.SearchForm__input {
  font-size: 1.2rem;
  border: none;
  background: inherit;
  outline: none;
  padding: 5px 10px;
  min-width: 140px;
  max-width: 300px;
}

.xbutton {
  width: 20px;
}
.SearchForm__input::-webkit-input-placeholder {
  font-size: 1.4rem;
  color: #d6be98;
}
.SearchForm__input:-ms-input-placeholder {
  font-size: 1.4rem;
  color: #d6be98;
}
.SearchForm__input::-ms-input-placeholder {
  font-size: 1.4rem;
  color: #d6be98;
}
.SearchForm__input::placeholder {
  font-size: 1.4rem;
  color: #d6be98;
}

.showing {
  padding: 0 10px;
}



